const messageReducer = (state = initialState, action) => {
  switch (action.type) {
  case 'SET_FILTER':
    return state
  case 'SET_RATING_FROM':
    return state
  default:
    return state
  }

}

export const genderChange = gender => {
  return {
    type: 'SET_FILTER',
    gender,
  }
}



export default messageReducer