const mailsReducer = (state = initialState, action) => {
  switch (action.type) {
  case 'REMOVE_MAIL':{
    const newState = state.filter( mail => !action.mailsDelete.includes(mail.id) )
    return newState}
  case 'SEND_MAIL': {
    console.log(action.data)
    const newMail = action.data
    const newState = state.concat(newMail)

    return newState
  }

  default:
    return state
  }

}
const generateId = () =>
  Number((Math.random() * 1000000).toFixed(0))


export const sendMail = (text, recipient, user) => {
  return {
    type: 'SEND_MAIL',
    data: {
      id: generateId(),
      recipient,
      owner: user.id,
      ownerName: user.name,
      ownerPhoto: user.photo,
      text,
      type: '',
      isReaded: false,
      data: '12/04/12:50',
    }
  }
}

export const removeMail = (mailsDelete) => {
  return {
    type: 'REMOVE_MAIL',
    mailsDelete,
  }
}



export default mailsReducer